import Echo from '../src/components/echo.vue'

const install = (Vue, options = {}) => {
  Vue.component('echo', Echo)
}

// auto install
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

const API = {
  install
}

export default API



